import React from 'react'
import Fade from 'react-reveal/Fade'

const About = () => {
  return (
    <div className="about container">
      <div className="row">
        <div className="col-12">
          <h3>About</h3>
          <Fade left>
            <p className="about-me p-3">
                I&apos;m a full stack developer with the skills to create modern web applications.
                I specialize in using React.js on the front end and Rails on the back end. If you are
                interested in learning more or would like to contact me directly, please visit the Contact section below.
              <br />
              <br />
                I decided to become a developer because I enjoy the process finding creative and efficient
                ways to solve problems.  Whether it&apos;s figuring out the best way to make that new
                feature work or learning which new technology is best suited for our project, the process
                of learning and growing is a one that I&apos;ve come to love.
              <br />
              <br />
                When I&apos;m not coding, I love to go skiing or play my guitar.
            </p>
          </Fade>
        </div>
      </div>
      <div className="row icons mt-2">
        <Fade right>
          <div className="icon-group col-sm-12 col-md-6 col-lg-3 p-0">
            <i className="devicon-atom-original-wordmark m-2"></i>
            <i className="devicon-bootstrap-plain-wordmark m-2"></i>
            <i className="devicon-css3-plain-wordmark m-2"></i>
            <i className="devicon-express-original m-2"></i>
          </div>
          <div className="icon-group col-sm-12 col-md-6 col-lg-3 p-0">
            <i className="devicon-git-plain-wordmark m-2"></i>
            <i className="devicon-github-plain m-2"></i>
            <i className="devicon-heroku-plain-wordmark m-2"></i>
            <i className="devicon-html5-plain-wordmark m-2"></i>
          </div>
          <div className="icon-group col-sm-12 col-md-6 col-lg-3 p-0">
            <i className="devicon-javascript-plain m-2"></i>
            <i className="devicon-jquery-plain-wordmark m-2"></i>
            <i className="devicon-mongodb-plain-wordmark m-2"></i>
            <i className="devicon-nodejs-plain-wordmark m-2"></i>
          </div>
          <div className="icon-group col-sm-12 col-md-6 col-lg-3 p-0">
            <i className="devicon-rails-plain-wordmark m-2"></i>
            <i className="devicon-react-original m-2"></i>
            <i className="devicon-ruby-plain m-2"></i>
            <i className="devicon-sass-original m-2"></i>
          </div>
        </Fade>
      </div>
    </div>
  )
}

export default About



