import React from 'react'
import Fade from 'react-reveal/Fade'

const Contact = () => {
  return (
    <div className="contact">
      <h3>Contact</h3>
      <Fade left>
        <div>
          <a className="mx-2" href="mailto:chuck.curtis.su@gmail.com">
            <img src="https://img.icons8.com/color/96/000000/gmail.png"/>
          </a>
          <a className="mx-2" onClick={() => window.open('https://www.linkedin.com/in/chuck-curtis/', '_blank')}>
            <img src="https://img.icons8.com/color/104/000000/linkedin.png"/>
          </a>
          <a className="mx-2" onClick={() => window.open('https://github.com/cpcurtis1218', '_blank')}>
            <img src="https://img.icons8.com/windows/96/000000/github.png"/>
          </a>
        </div>
      </Fade>
    </div>
  )
}

export default Contact



